@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
* {
    margin: 0;
    padding: 0;
}


/*About style*/
.about {
    font-family: monospace;
    min-height: 100vh;
    color: #232d36;
    background-color: #fde311;
    padding: 6vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 3rem;
    /*height: 60vh;*/
}
.typical p{
    font-size: 2rem;
    font-family: monospace;

}

.story-description p{
    padding: 6vh;
    font-size: 1rem;
    font-family: monospace;
    margin: 0 5rem;
    color: #232d36;
    letter-spacing: 1px;
}


/*Home style*/
.home{
    display: flex;
    height: 60vh;
    flex-direction: column;
    align-items: center;
    font-size: 2rem;
}
.profile-img {
    border-radius: 50%;
    height: 50vh;
    border: #90ac58 1px;
    margin-top: 50px;

 }
.link-icons {
    text-decoration: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 3px;
    cursor: pointer;
}


.navbar {
    background-color: #bb0548e0;
    height:80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}
.nav-menu{
    background-color: #bb0548e0;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}
.nav-menu.active{
    left: 0;
    transition: 350ms;
}
.nav-text{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0 8px 16px;
    list-style: none;
    height: 60px;
}
.nav-text a{
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}
.nav-text a:hover{
    background-color: gold;
}
.nav-menu-items {
    width:100%;
    color: #f5f5f5;
}
.navbar-toggle{
    background-color: #bb0548e0;
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
span {
    margin-left: 10px;
}
.contact{
    background-image: url(/static/media/yellowall.b1d8a916.png );
    height:90vh;
    width: 100%;
    background-size: contain;
    color: #bb052fe0;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
.formContact {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    font-family: monospace;
    top: 10%;
    left: 10%;
    width: 70%;
    height: 50vh;
    border-radius: 1rem;
    padding: 4rem 2rem;

}
.labelName {
    width: 80%;
    top:0;
    left: 0;
    margin-bottom: 10px;
   font-size: 20px;
    color: darkred;
    border: 2px solid #bb052fe0;
    border-radius: .5rem;
    outline:none;
    padding: 1rem;
    background: none;
    box-shadow: 0 10px 15px rgb(128,0,32);

}
input[type="text"] {
    border-bottom: 1px solid crimson;
    background: transparent;
    border-top: none;
    border-left: none;
    border-right: none;
    margin-left: 10px;
    width: 90%;
    font-size: 20px;
    height: 30%;
    color: crimson;
    font-family: monospace;
    outline: none;
    padding-top: 1rem;
    padding-bottom: 0.5rem;
}
.labelEmail {
    width: 80%;
    top:0;
    left: 0;
    margin-bottom: 10px;
    font-size: 20px;
    color: darkred;
    border: 2px solid #bb052fe0;
    border-radius: .5rem;
    outline:none;
    padding: 1rem;
    background: none;
    box-shadow: 0 10px 36px rgb(128,0,32);
}
input[type="email"] {
    border-bottom: 1px solid crimson;
    background: transparent;
    border-top: none;
    border-left: none;
    border-right: none;
    margin-left: 10px;
    padding-top: 1rem;
    padding-bottom: 0.5rem;
    width: 90%;
    font-size: 20px;
    height: 30%;
    color: crimson;
    font-family: monospace;
    outline: none;
}
.labelMessage {
    width: 80%;
    height: 20%;
    top:0;
    left: 0;
    margin-bottom: 10px;
    font-size: 20px;
    color: darkred;
    border: 2px solid #bb052fe0;
    border-radius: .5rem;
    outline:none;
    padding: 1rem;
    background: none;
    box-shadow: 0 10px 36px rgb(128,0,32);
}
textarea{
    border-bottom: 1px solid crimson;
    background: transparent;
    border-top: none;
    border-left: none;
    border-right: none;
    margin-left: 10px;
    width: 90%;
    font-size: 20px;
    height: 50%;
    color: crimson;
    font-family: monospace;
    outline: none;
    padding-top: 1rem;
    padding-bottom: 0.5rem;
}
.sendButton {
    display: block;
    margin-left: auto;
    padding: 1rem 2rem;
    outline: none;
    border: none;
    background-color: crimson;
    color: #fde311;
    font-size: 20px;
    border-radius: 2rem;
    cursor: pointer;
    transition: 3s;
}
.sendButton:hover{
    box-shadow: 0 10px 36px rgb(128,0,32);
}
input[type="submit"] {
    background: transparent;
    border: none;
    font-family: monospace;
    font-size: 20px;
    color: gold;
}
